/* selection container */
.selection {
  position: relative;
}

/* selection box */
.selection-border {
  background: rgba(0, 162, 255, 0.4);
  position: absolute;
  z-index: 99;
}

.selected {
  background-color: var(--oc-orange-9) !important;
}

